@keyframes show-banner {
  from {
    visibility: hidden;
    opacity: 0; }
  to {
    visibility: visible;
    opacity: 1; } }

@-webkit-keyframes show-banner {
  from {
    visibility: hidden;
    opacity: 0; }
  to {
    visibility: visible;
    opacity: 1; } }

@keyframes product-show {
  from {
    visibility: hidden;
    opacity: 0;
    top: 20px; }
  to {
    visibility: visible;
    opacity: 1;
    top: 0; } }

@-webkit-keyframes product-show {
  from {
    visibility: hidden;
    opacity: 0;
    top: 20px; }
  to {
    visibility: visible;
    opacity: 1;
    top: 0; } }

@-webkit-keyframes mymove {
  from {
    width: 0px;
    height: 0px; }
  to {
    width: 30px;
    height: 30px;
    opacity: 0; } }

@keyframes mymove {
  from {
    width: 0px;
    height: 0px; }
  to {
    width: 30px;
    height: 30px;
    opacity: 0; } }

.menu {
